html {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0;
    padding: 0;
}

*,
*::before,
*::after {
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
}

body {
    min-width: 320px;
    margin: 0;
    padding: 0;
    font-size: 18px;
    line-height: 27px;    
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    font-style: normal;
  }
  
  img {
    max-width: 100%;
    vertical-align: top;
  }